import React, {Component} from 'react';
import { connect } from 'react-redux';
import CardList from '../Components/CardList';
import SearchBox from '../Components/searchBox';
import Scroll from '../Components/scroll';

import { setSearchField, requestRobots } from '../actions';

const mapStateToProps = state => {
  return {
    searchField: state.searchRobots.searchField,
    users: state.requestRobots.robots,
    isPending: state.requestRobots.isPending,
    error: state.requestRobots.error
  }
}
const mapDispatchToProps = (dispatch) => {
  return { 
    onSearchChange: (event) => dispatch(setSearchField(event.target.value)),
    onRequestRobots: () => dispatch(requestRobots())
  }
}

class App extends Component{
  
  componentDidMount(){
    this.props.onRequestRobots();
  }


  render(){
    const { searchField, onSearchChange, users, isPending } = this.props;
    
    const filterRobots = users.filter(user => {
      return user.name.toLowerCase().includes(searchField.toLowerCase());
    })

    return isPending ?
      <div className="tc">
        <h2 id="header" className="f1 tc">Loading...</h2>
      </div> :
      <div className="tc">
        <h2 id="header" className="f1 tc">Robo Friends</h2>
        <SearchBox searchChange={onSearchChange} />
        <Scroll>
          <CardList robots={filterRobots} />
        </Scroll>
      </div>
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
